* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Times New Roman', Times, serif;
}

body {
    color: white;
    background-color: #1a1814;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 90px;
    padding: 0 10%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 100;
    background-color: #1a1814;
    transition: height 0.3s ease;
}

.header.scrolled {
    height: 90px; /* Ensures height stays consistent */
}

.logo {
    font-size: 25px;
    text-decoration: none;
    font-weight: 600;
    color: #cda45e;
}

.logo:hover {
    color: #ffffff;
    text-decoration: none;
}

.navbar-items a {
    display: inline-block;
    font-size: 25px;
    color: #cda45e;
    text-decoration: none;
    font-weight: 500;
    margin-left: 35px;
    transition: color 0.3s ease;
}

.navbar-items a:hover {
    color: #ffffff;
}

.navbar-toggle {
    display: none;
    font-size: 25px;
    color: #cda45e;
    cursor: pointer;
}

@media (max-width: 768px) {
    .navbar-items {
        display: none;
        flex-direction: column;
        width: 100%;
        background-color: #1a1814;
        position: absolute;
        top: 100%;
        left: 0;
        padding: 10px 0;
        border-top: 1px solid #cda45e;
    }

    .navbar-items a {
        margin: 10px 0;
        text-align: center;
        width: 100%;
    }

    .navbar-toggle {
        display: block;
    }

    .navbar-items.active {
        display: flex;
    }
}
