body {
    font-family: 'Open Sans', sans-serif;
    background-color: #000000; 
    color: #cda45e;
    padding: 0;
    position: relative;
}

.profile-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 90px; 
    background-color: #000000; 
    padding: 0 20px;
}


.profile-parent {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #1c1f26; 
    border-radius: 10px;
    padding: 20px;
}

.profile-details {
    text-align: center;
    flex: 1;
    margin-right: 0px;
}

.colz {
    margin-bottom: 20px;
}

.colz-icon a {
    color: #cda45e; 
    font-size: 26px;
    margin: 0 10px;
    transition: color 0.3s;
}

.colz-icon i:hover {
    color: #5a5a5a; /* Highlight Color */
    transform: scale(1.5);
}

.profile-details-name {
    margin-bottom: 20px;
}

.primary-text {
    font-size: 40px;
    font-weight: 900;
    color: white;
}

.highlighted-text {
    color: #cda45e; 
}

.profile-details-role {
    margin-bottom: 20px;
}

.profile-details-role h1 {
    font-size: 40px;
    margin: 0;
    padding: 0;
    color: #ffffff; 
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: 600;
}

.profile-role-tagline {
    font-size: 18px;
    color: #cda45e; 
}

.profile-options {
    margin-top: 20px;
}

.btn {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 20px;
    cursor: pointer;
    margin: 5px;
    transition: background-color 0.3s, color 0.3s;
    font-family: 'Times New Roman', Times, serif;
}

.primary-btn {
    background-color: #5a5a5a;
    color: #ffffff; 
}

.primary-btn:hover {
    background-color: #464444; 
    color: #ffffff;
}

.highlighted-btn {
    background-color: #cda45e;
    color: #000000; 
}

.highlighted-btn:hover {
    background-color: #b58d40; 
}

.profile-picture {
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 2px 1px 0px #cda45e;
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin: 0 0px 0 100px;
}

.profile-picture-background {
    width: 92%;
    height: 92%;
    border-radius: 50%;
    background-image: url('../../assets/pictures/profilePhoto.jpeg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.profile-picture-background:hover {
    transform: scale(1.07);
    transition: 1s ease-out;
}

@media (max-width: 768px) {
    .profile-container {
        flex-direction: column;
        align-items: center;
    }

    .profile-parent {
        flex-direction: column;
        align-items: center;
        padding: 20px 10px;
    }

    .profile-details {
        margin-right: 0;
        margin-bottom: 20px;
    }

    .profile-picture {
        height: 300px;
        width: 300px;
        margin: 0;
    }
}
