#contact {
  background-color: black;
  color: #ffffff;
  padding: 60px 0;
}

.c-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  gap: 20px;
}

.contact-header {
  text-align: center;
  margin-top: 2%;
  margin-bottom: 40px;
  animation: fadeInDown 1s ease-out;
}

.contact-header h1 {
  color: #cda45e;
  font-size: 48px;
  margin-bottom: 10px;
}

.contact-header .underline {
  width: 50%;
  height: 4px;
  background-color: #cda45e;
  margin: 0 auto;
}

.contact-form {
  flex: 1;
  max-width: 600px;
  background-color: #1c1f26;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-right: 20px;
  animation: fadeInLeft 1s ease-out;
}

.contact-form h2 {
  color: #cda45e;
  font-size: 36px;
  margin-bottom: 20px;
  text-align: center;
}

.contact-form .form-group {
  margin-bottom: 20px;
}

.contact-form label {
  display: block;
  margin-bottom: 5px;
  font-weight: 600;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #37332a;
  border-radius: 5px;
  background-color: #0c0b09;
  color: #ffffff;
  transition: border-color 0.3s ease-in-out;
}

.contact-form input:focus,
.contact-form textarea:focus {
  border-color: #cda45e;
}

.contact-form .button {
  width: 100%;
  padding: 15px;
  background-color: #cda45e;
  border: none;
  border-radius: 5px;
  color: #0c0b09;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.contact-form .button:hover {
  background-color: #a68943;
}

.success-message,
.error-message {
  text-align: center;
  font-size: 18px;
  margin-top: 20px;
}

.success-message {
  color: #4caf50;
  animation: fadeInUp 0.5s ease-out;
}

.error-message {
  color: #f44336;
  animation: fadeInUp 0.5s ease-out;
}

.map-container {
  flex: 1;
  max-width: 600px;
  position: relative;
  animation: fadeInRight 1s ease-out;
}

.map-container iframe {
  width: 100%;
  height: 100%;
  border: 0;
  border-radius: 10px;
  min-height: 400px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
}

.map-container iframe:hover {
  transform: scale(1.05);
}

@media (max-width: 1200px) {
  .contact-form h2 {
    font-size: 32px;
  }

  .contact-form .button {
    font-size: 16px;
  }
}

@media (max-width: 992px) {
  .c-container {
    flex-direction: column;
    gap: 20px;
  }

  .contact-form,
  .map-container {
    max-width: 100%;
  }

  .contact-form {
    margin-right: 0;
  }
}
