#about {
    background-color: #0c0b09;
    color: #ffffff;
    padding: 60px 20px;
    margin-top: 27px;
    margin-bottom: 20px;
}

.about-container {
    max-width: 1000px;
    margin: 0 auto;
}

.about-content {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 30px;
    justify-content: center;
}

.about-image img {
    border-radius: 50%;
    width: 300px;
    height: 300px;
    object-fit: cover;
    border: 5px solid #cda45e;
    transition: transform 0.3s ease, box-shadow 0.3s ease; 
}

.about-image img:hover {
    transform: scale(1.1); 
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); 
}

.about-text-container {
    text-align: left;
    max-width: 600px;
}

.about-text-container h2 {
    left: 25%;
    font-size: 3em;
    color: #cda45e; 
    position: relative;
    margin-bottom: 30px;
    padding-bottom: 10px;
}

.about-text-container h2::before {
    content: "";
    position: absolute;
    left: 15%;
    bottom: 0;
    transform: translateX(-50%);
    width: 65%;
    height: 5px;
    background: #cda45e; 
    border-radius: 5px;
}

.about-text-container h2::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: -10px;
    transform: translateX(-50%);
    width: 50%;
    height: 3px;
    background: rgba(255, 255, 255, 0.5);
    border-radius: 5px;
    z-index: -1;
}

.about-text {
    font-size: 1.2em;
    line-height: 1.8;
}

@media (max-width: 768px) {
    .about-content {
        flex-direction: column;
        gap: 20px;
        text-align: center;
    }

    .about-text-container {
        text-align: center;
    }

    .about-text-container h2 {
        font-size: 2.5em;
        left: 1%;
    }

    .about-text-container h2::before {
        left: 50%;
        width: 50%;
    }

    .about-text-container h2::after {
        left: 50%;
        width: 40%;
    }

    .about-text {
        font-size: 1em;
        line-height: 1.6;
    }

    .about-image img {
        width: 150px;
        height: 150px;
    }
}

@media (max-width: 480px) {
    .about-text-container h2 {
        left:0%;
        font-size: 2em;
    }

    .about-text-container h2::before {
        left: 50%;
        width: 40%;
    }

    .about-text-container h2::after {
        left: 20%;
        width: 30%;
    }

    .about-content {
        gap: 15px;
    }

    .about-text {
        font-size: 0.9em;
    }

    .about-image img {
        width: 120px;
        height: 120px;
    }
}
