#footer {
    background-color: #1a1814;
    color: #ffffff;
    padding: 20px 0;
    text-align: center;
    font-family: 'Open Sans', sans-serif;
  }
  
  .footer-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .footer-content {
    width: 80%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .footer-info, .footer-social, .footer-links {
    flex: 1;
    margin: 10px;
  }
  
  .footer-info h3, .footer-social h3, .footer-links h3 {
    color: #cda45e;
    margin-bottom: 10px;
  }
  
  .footer-info p, .footer-social a, .footer-links a {
    color: #ffffff;
    margin: 5px 0;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .footer-social a:hover, .footer-links a:hover {
    color: #d9ba85;
  }
  
  .footer-social a, .footer-links a {
    display: block;
  }
  
  .footer-bottom {
    margin-top: 20px;
    padding-top: 10px;
    border-top: 1px solid #37332a;
    width: 100%;
    text-align: center;
  }
  
  .footer-bottom a {
    color: #cda45e;
    text-decoration: none;
    margin: 0 10px;
  }
  
  .footer-bottom a:hover {
    color: #d9ba85;
  }
  
  .back-to-top {
    margin-top: 20px;
    background-color: #cda45e;
    color: #1a1814;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .back-to-top:hover {
    background-color: #d9ba85;
  }
  
  .back-to-top-icon {
    margin-left: 10px;
  }
  