#skills {
  margin-top: 3%;
}

.skills-container {
  padding: 20px;
  background-color: #1a1814;
  color: #cda45e;
  text-align: center;
  margin-bottom: 3%;
}

.skills-header {
  text-align: center;
  margin-top: 2%;
  margin-bottom: 40px;
  animation: fadeInDown 1s ease-out;
}

.skills-header h1 {
  color: #cda45e;
  font-size: 48px;
  margin-bottom: 10px;
}

.skills-header .underline {
  width: 50%;
  height: 4px;
  background-color: #cda45e;
  margin: 0 auto;
}

.skills-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.skill {
  position: relative;
  width: 150px;
  height: 150px;
  transition: transform 0.3s;
}

.skill:hover {
  transform: scale(1.1);
}

.circular-progress {
  width: 100%;
  height: 100%;
}

.circle-bg {
  fill: #1c1f26;
  stroke: #dac283e8;
  stroke-width: 2.8;
}

.circle {
  fill: none;
  stroke: #b68635;
  stroke-width: 2.3;
  stroke-linecap: round;
  transition: stroke-dasharray 0.3s;
}

.skill-name {
  fill: #cda45e;
  font-size: 0.4em;
  text-anchor: middle;
}

@media (max-width: 1024px) {
  .skills-container {
    padding: 15px;
  }

  .skills-header h1 {
    font-size: 40px;
  }

  .skills-header .underline {
    width: 60%;
  }

  .skill {
    width: 120px;
    height: 120px;
  }
}

@media (max-width: 768px) {
  .skills-grid {
    flex-direction: row;
    justify-content: space-around;
  }

  .skill {
    width: 100px;
    height: 100px;
  }
}

@media (max-width: 480px) {
  .skills-container {
    padding: 10px;
  }

  .skills-header h1 {
    font-size: 32px;
  }

  .skills-header .underline {
    width: 70%;
  }

  .skills-grid {
    justify-content: center;
  }

  .skill {
    width: 80px;
    height: 80px;
  }
}

@media (max-width: 360px) {
  .skills-grid {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }

  .skill {
    width: 80px;
    height: 80px;
    flex: 0 1 calc(33.333% - 20px);
    margin: 10px;
  }
}
