#projects {
  background-color: black;
  color: #ffffff;
  padding: 60px 0;
}

.section-title {
  text-align: center;
  margin-bottom: 40px;
}

.section-title h2 {
  color: #cda45e;
  font-size: 40px;
  transition: color 0.3s ease-in-out;
}

.container {
  align-items: center;
  width: 85%;
  max-width: 1400px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #1c1f26;
  border-radius: 10px;
  padding: 20px;
  margin-top: 2%;
  transition: background-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.navbar {
  align-items: center;
  display: flex;
  justify-content: center;
}

.nav-tabs {
  display: flex;
  justify-content: center;
  border: none;
  cursor: pointer;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 600;
  transition: font-size 0.3s ease-in-out;
}

.nav-tabs .nav-link {
  color: #ffffff;
  background-color: #0c0b09;
  margin: 0 5px;
  padding: 15px;
  border: 1px solid #37332a;
  border-radius: 5px 5px 0 0;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.nav-tabs .nav-link:hover {
  background-color: #cda45e;
  color: #0c0b09;
}

.nav-tabs .nav-link.active {
  background-color: #cda45e;
  color: #0c0b09;
}

.tab-content .tab-pane {
  padding: 20px;
  background-color: #0c0b09;
  border: 4px solid #37332a;
  border-radius: 8px;
  margin-bottom: 20px;
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}

.details {
  margin-bottom: 20px;
}

.details h3 {
  color: #cda45e;
  margin-bottom: 15px;
  font-size: 30px;
  text-align: center;
  animation: fadeInUp 1s ease-in-out;
}

.details .fst-italic {
  color: #d9ba85;
  font-size: 20px;
  animation: fadeInUp 1.2s ease-in-out;
}

.details p {
  color: #ffffff;
  font-size: 20px;
  animation: fadeInUp 1.4s ease-in-out;
}

.video-container {
  text-align: center;
  margin-top: 6%;
}

.video-container video {
  width: 90%;
  max-width: 600px;
  height: auto;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
}

.video-container video:hover {
  transform: scale(1.05);
  opacity: 0.8;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 1200px) {
  .nav-tabs {
    font-size: 18px;
  }

  .details h3 {
    font-size: 28px;
  }

  .details .fst-italic,
  .details p {
    font-size: 18px;
  }
}

@media (max-width: 768px) {
  .container {
    width: 90%;
  }

  .nav-tabs {
    flex-direction: column;
    font-size: 16px;
  }

  .nav-tabs .nav-link {
    margin-bottom: 10px;
    border-radius: 5px;
  }

  .details h3 {
    font-size: 26px;
  }

  .details .fst-italic,
  .details p {
    font-size: 16px;
  }
}

@media (max-width: 576px) {
  .nav-tabs {
    font-size: 14px;
  }

  .details h3 {
    font-size: 24px;
  }

  .details .fst-italic,
  .details p {
    font-size: 14px;
  }
}
